@import url(https://fonts.googleapis.com/css2?family=Didact+Gothic&display=swap);
body{margin:0;font-family:"Didact Gothic",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}#root{height:100vh;background-size:cover;background-attachment:fixed;background-position:center;background-image:url(/router/static/media/back.59ec9e1d.jpg);overflow-y:scroll}
nav{display:flex;justify-content:space-around;padding:30px 0;transition:.3s ease-in-out}nav:hover{background-color:rgba(247,185,216,.74)}nav li{font-weight:700;letter-spacing:.1em}nav li,nav a{list-style-type:none;text-decoration:none;color:#333;transition:.3s ease-in-out;font-size:28px}nav li:hover,nav a:hover{color:#c00261}
.quote{margin:50px auto;width:-webkit-max-content;width:-moz-max-content;width:max-content;color:#333;text-transform:uppercase;padding:30px;background-color:rgba(247,199,232,.76)}.two_sides_block{display:flex;justify-content:center}.two_sides_block .letter{background-color:rgba(247,199,232,.712);color:#333;font-size:18px;width:30%;padding:40px}.two_sides_block .letter strong{color:#c00261;font-size:20px}.two_sides_block .imageWrapper{position:relative}.two_sides_block .imageWrapper .overlay{position:absolute;background-color:rgba(247,199,232,.288);width:300px;height:100%;top:0;left:0;z-index:2;transition:.3s ease}.two_sides_block .imageWrapper .overlay:hover{opacity:0}.two_sides_block .imageWrapper .me{width:300px;height:100%;position:relative}
.photos{width:80%;margin:0 auto;display:flex;justify-content:space-between;flex-wrap:wrap}.photos .photos_post{position:relative;width:300px;height:-webkit-max-content;height:-moz-max-content;height:max-content;margin:3%;transition:.3s ease-in-out;cursor:pointer}.photos .photos_post .photos_overlay{position:absolute;z-index:2;width:200px;opacity:0;padding:50px;font-weight:600;color:#333}.photos .photos_post .photos_overlay:hover{opacity:1;background-color:rgba(255,198,241,.8)}.photos .photos_post>img{width:100%}.photos .photos_post:hover{-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-filter:drop-shadow(0px 0px 1px rgba(0, 0, 0, 0.3)) drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.3));filter:drop-shadow(0px 0px 1px rgba(0, 0, 0, 0.3)) drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.3))}
.post_wrapper{margin:10% auto;width:-webkit-max-content;width:-moz-max-content;width:max-content}.post{background-color:#333;padding:20px;display:flex;width:700px}.photo img{width:70px;height:70px;border-radius:100%}.headLine{display:flex;margin:-15px 0}.author{color:#fafafa;font-size:20px}.sub{color:#999;font-size:18px;line-height:3.2em;margin:0 5px}.content{color:#fafafa;margin:-10px 0}.post_container{margin-left:10px}.post_image{border-radius:20px;width:100%;margin:23px 0}
